body{
  background-color: black;
  display: flex;
  min-height: 100vh;
  justify-content: center;
  font-family: "Poppins", sans-serif;
}
.non-scrollable{
  overflow-y: hidden;
}
/* Header */
#section{
  font-size: 32px;
  display: flex;
  justify-content: center;
  position: relative;
}
h2:nth-child(1){
	/* color: transparent; */
  display: inline-block;
  z-index: 1;
	/* -webkit-text-stroke: 2px #fff; */
  margin: 20px 0px;
  color: #000;
  animation: animate 4s ease-in-out infinite;
}
@keyframes animate {
	0%,
	100% {
		clip-path: polygon(
			0% 45%,
			16% 44%,
			33% 50%,
			54% 60%,
			70% 61%,
			84% 59%,
			100% 52%,
			100% 100%,
			0% 100%
		);
	}

	50% {
		clip-path: polygon(
			0% 60%,
			15% 65%,
			34% 66%,
			51% 62%,
			67% 50%,
			84% 45%,
			100% 46%,
			100% 100%,
			0% 100%
		);
	}
}
h2:nth-child(2){
  top: -20px;
  position: absolute;
  color: white;
  -webkit-text-stroke: 2px #fff;

}

/* Modal  */
.modal{
  position: fixed;
  min-height: 100vh;
  min-width: 100vw;
  top: 0;
  left: 0;
  background-color: rgba(0,0,0,0.3);
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal .content{
  width: 60%;
  background-color: white;
  display: flex;
  padding: 20px;
  gap: 8px;
  flex-wrap: wrap;
  position: relative;
  border-radius: 4px;
}
.modal img {
  width: 150px;
}
#pokemon-preview{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  text-transform: capitalize;
  text-decoration: underline;
  font-weight: 700;
  flex-grow: 1;
  
}
.modal .pokemon-info-container{
  display: flex;
  flex-grow: 1;
  justify-content: center;
}
.modal .pokemon-info{
  display: flex;
  gap: 8px;
  padding: 4px 8px;
  flex-wrap: wrap;
  justify-content: center;
}
.modal .pokemon-info--section{
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 4px;
}
.modal #close{
  position: absolute;
  top: 10px;
  right: 10px;
  width: 20px;
  height: 20px;
  padding: 5px;
  border-radius: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-family: "Poppins", sans-serif;
}
/* #inactive{
  display: none;
} */

/* Main Section  */
.pokemon-container{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 8px;
}
.card{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 16px;
  border: 1px solid white;
  border-radius: 2px;
  min-width: 180px;
  text-align: center;
  transition: transform 0.3s ease-in-out;
  text-transform: capitalize;
}
.card:hover{
  transform: scale(1.1);
  border-radius: 6px;
}
.card .number{
  width: 20px;
  height: 20px;
  border-radius: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 6px;
  font-size: 14px;
  margin-bottom: 4px;
  background-color: rgba(255,255,255,0.3);
}
.details h3{
  padding-top:  8px;
  margin: 0px;

}
.card img{
  width: 120px;
  height: 120px;
}

/* Cards background styling */
.rock {
  background-image: linear-gradient(to top, #c79081 0%, #dfa579 100%);
}
.ghost {
  
  background-image: linear-gradient(to top, #b7b7b8 0%, #e2ebf0 100%);
}
.electric {
  
  background-image: linear-gradient(to right, #f83600 0%, #f9d423 100%);
}
.bug {
  
  background-image: linear-gradient(to top, #e6b980 0%, #eacda3 100%);
}
.poison {
  
  background-image: linear-gradient(to top, #df89b5 0%, #bfd9fe 100%);
}
.normal {
  
  background-image: linear-gradient(-225deg, #E3FDF5 0%, #FFE6FA 100%);
}
.fairy {
  
  background-image: linear-gradient(to top, #ff9a9e 0%, #fecfef 99%, #fecfef 100%);
}
.fire {
  
  background-image: linear-gradient(120deg, #f6d365 0%, #fda085 100%);
}
.grass {
  
  background-image: linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%);
}
.water {
  
  background-image: linear-gradient(120deg, #89f7fe 0%, #66a6ff 100%);
}
.ground{
  
  background-image: linear-gradient(315deg, #772f1a 0%, #f2a65a 74%);
  
}
